import React from 'react'
import withStyles from '@material-ui/core/styles/withStyles'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import CardMedia from '@material-ui/core/CardMedia'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import Chip from '@material-ui/core/Chip'
import CardHeader from '@material-ui/core/CardHeader'
import GithubIcon from '../components/githubIcon'
import GogsIcon from '../components/gogsIcon'
import DownloadIcon from '../components/downloadIcon'
import LinkIcon from '@material-ui/icons/Launch'
import ReactPlayer from 'react-player'

/**
 * Preview Card
 * 
 * Card for showcasing the basic information of a project.
 */

const styles = theme => ({
    card:{
    },
    preview:{
    },
    chip:{
        marginRight: theme.spacing.unit,
        marginBottom: theme.spacing.unit,
    },
})

/**
 * PreviewCard
 * 
 * @description display information of work in card
 * @param data - object, with fields: title, detail, video, link, github, vimeo, genre, brief, build, image, gogs, animation
 */

class PreviewCard extends React.Component {

    constructor(props){
        super(props)
        this.props.data.genre = this.props.data.genre.sort()
    }
    

    static defaultProps = {
        data: {
            title:"No title",
            detail:"No description",
            video:"",
            link:"",
            github:"",
            vimeo:"",
            genre:[],
            brief:"No description",
            build:"",
            image:"",
            gogs:"",
            animation:"",
        }
    }

    state = { expanded: false, open: false };

    handleExpandClick = () => {
        this.setState(state => ({ expanded: !state.expanded }));
    }

    handleOpenVideoModal = () =>{
        this.setState({open: true})
    }

    handleCloseVideoModal = () => {
        this.setState({open: false})
    }

    render() {
        const { classes, data }  = this.props;
        const { brief, detail, title, video, link, github, genre, build, image, vimeo, animation, gogs} = data;

        return(
            <div>
                <Card className={classes.card} elevation={0}>

                    {/* Show either one of the action */}
                    <CardHeader title={title} subheader={brief} action={
                        ( github &&
                            <IconButton href={github} target="_blanks" aria-label="github link">
                                <GithubIcon/>
                            </IconButton>
                        ) || ( gogs &&
                            <IconButton href={gogs} target="_blanks" aria-label="gogs link">
                                <GogsIcon/>
                            </IconButton>
                        ) || ( build &&
                            <IconButton href={build} aria-label="build link" target="_blanks">
                                <DownloadIcon/>
                            </IconButton>
                        ) || ( link && 
                            <IconButton href={link} target="_blanks" aria-label="link">
                                <LinkIcon/>
                            </IconButton>
                        ) 
                    }/>
          
                    { (video || vimeo || animation ) ?

                        <CardMedia
                            title={title}
                            src={video || vimeo || animation}
                            component={()=> (
                                <div style={{position:"relative", paddingTop:"56.25%"}}>
                                    
                                    {/* 
                                        manage mp4 as gif (animation)
                                        ref: https://developers.google.com/web/fundamentals/performance/optimizing-content-efficiency/replace-animated-gifs-with-video/
                                    */}
                                    { animation && <ReactPlayer
                                        style={{position:"absolute", top:0, left:0, bottom: 0, right: 0}}
                                        url={animation}
                                        width="100%"
                                        height="100%"
                                        loop
                                        volume={0}
                                        muted 
                                        playsinline
                                        playing
                                    />}

                                    { video && <ReactPlayer
                                        style={{position:"absolute", top:0, left:0, bottom: 0, right: 0}}
                                        url={video}
                                        width="100%"
                                        height="100%"
                                        controls
                                    />}

                                    { vimeo && <ReactPlayer
                                        style={{position:"absolute", top:0, left:0, bottom: 0, right: 0}}
                                        url={vimeo}
                                        width="100%"
                                        height="100%"
                                        controls
                                    />}

                                </div>
                            )}
                        /> 
                    :
                    <CardMedia
                        className={classes.preview}
                        image={image}
                        title={title}
                        component='img'
                    />
                    }
                    <CardContent>
                        {detail &&
                            detail.split("\n").map( (line,index) => (
                                <Typography key={title.concat(index)} paragraph gutterBottom> {line} </Typography>
                        ))}

                        {
                            genre.map(tag => (
                                <Chip key={title+tag} label={tag} className={classes.chip}/>
                            ))
                        }
                    </CardContent>

                </Card>
            </div>
        )
    }
}

export default withStyles(styles)(PreviewCard);


